<template>
  <div class="editCamera">
    <el-tabs v-model="activeName">
      <el-tab-pane :label="title" name="first">
        <el-form
          ref="form"
          :model="cameraInfo"
          label-width="90px"
          label-position="left"
        >
          <el-form-item label="摄像头名字">
            <el-input v-model="cameraInfo.cameraName"></el-input>
          </el-form-item>
          <el-form-item label="账号">
            <el-input v-model="cameraInfo.account"></el-input>
          </el-form-item>
          <el-form-item label="密码">
            <el-input v-model="cameraInfo.password"></el-input>
          </el-form-item>
          <el-form-item label="ip地址">
            <el-input v-model="cameraInfo.ip"></el-input>
          </el-form-item>
          <el-form-item label="接入录像机">
            <el-switch
              v-model="cameraInfo.isInMachine"
              active-color="#13ce66"
              inactive-color="#ff4949"
            >
            </el-switch>
          </el-form-item>
          <el-form-item label="初始位置">
            <el-input v-model="cameraInfo.originPos"></el-input>
          </el-form-item>
          <el-form-item label="端口">
            <el-input v-model="cameraInfo.port"></el-input>
          </el-form-item>
          <el-form-item label="rtsp">
            <el-input v-model="cameraInfo.rtspUrl"></el-input>
          </el-form-item>
          <el-form-item label="通道号">
            <el-input v-model="cameraInfo.wayNum"></el-input>
          </el-form-item>
          <!-- <el-form-item class="buttons"> -->
          <el-button type="danger" round @click="saveInfo">保存</el-button>
          <!-- </el-form-item> -->
        </el-form>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
export default {
  props: ["cameraInfor",'isEdit'],
  data() {
    return {
      cameraInfo: {
        account: "",
        password: "",
        ip: "",
        isInMachine: false,
        originPos: "",
        port: "",
        rtspUrl: "",
        wayNum: "",
        cameraName: ""
      },
      activeName: "first",
      id: 1,
      title:'基本信息'
    };
  },
  computed: {},
  created() {},
  mounted() {},
  watch: {
    cameraInfor() {
      this.cameraInfo = { ...this.cameraInfor };
      console.log("cameraInfor", this.cameraInfor);
    },
    isEdit(){
      console.log(this.isEdit);
      if (this.isEdit) {
        this.title='基本信息'
      } else if(!this.isEdit){
        this.title='新增摄像头'
      } 
    }
  },
  methods: {
    saveInfo() {
      let status;
      if (this.cameraInfo.id) {
        status = "update";
        this.id = this.cameraInfo.id;
      } else {
        status = "save";
      }
      this.$axios.post(`/data/camera/${status}`, this.cameraInfo).then(res => {
        console.log("保存摄像头", res);
        this.$emit("closeEdit", status);
        this.id = 1;
        this.$message.success("保存成功");
      });
    }
  },
  components: {}
};
</script>

<style scoped lang="less">
.editCamera {
    padding: 0 20px;
  form {
    width: 500px;
    padding: 20px;
    .el-button {
      width: 100px;
    }
  }
}
</style>



