<template>
  <div class="deploy_content">
    <div class="left">
      <aside>
        摄像头列表
        <i class="el-icon-circle-plus" @click="addCamera"></i>
      </aside>

      <ul>
        <li
          v-for="(item, index) in cameraList"
          :key="index"
          @click="handleClick(item, index)"
          :class="{ active: currentIndex == index }"
        >
          {{ item.cameraName }}

          <i class="el-icon-circle-close" @click.stop="delCamera(item.id)"></i>
          <!-- <i v-if="!isEdit" class="el-icon-circle-close" @click="Cancel"></i> -->
        </li>
      </ul>
    </div>
    <div class="right">
      <edit-camera
        :cameraInfor="cameraInfor"
        :isEdit="isEdit"
        @closeEdit="closeEdit"
      ></edit-camera>
    </div>
  </div>
</template>

<script>
import editCamera from "@/pages/deploy/editCamera";
export default {
  props: [],
  data() {
    return {
      cameraList: [],
      cameraInfor: {},
      currentIndex: 0,
      isEdit: true
    };
  },
  computed: {},
  created() {},
  mounted() {
    this.getAllCamera();
  },
  watch: {},
  methods: {
    getAllCamera() {
      this.$axios.post("/data/camera/findAll").then(res => {
        console.log("摄像头列表", res);
        let cameraList = res.data;
        cameraList.sort(this.$sortForArray("id"));
        this.cameraList = cameraList;
        // this.cameraInfor = res.data.content.filter(item => {
        //   return item.id == this.currentIndex;
        // })[0];
        console.log("this.currentIndex", this.currentIndex);
        this.cameraInfor = this.cameraList[this.currentIndex];
      });
    },
    handleClick(info, index) {
      this.cameraInfor = info;
      this.currentIndex = index;
    },
    addCamera() {
      this.cameraList.push({ cameraName: "new" });
      this.cameraInfor = {
        account: "",
        password: "",
        ip: "",
        isInMachine: false,
        originPos: "",
        port: "",
        rtspUrl: "",
        wayNum: "",
        cameraName: ""
      };
      this.currentIndex = this.cameraList.length - 1;
      this.isEdit = false;
    },
    delCamera(id) {
      console.log("id", id);
      console.log("isEdit", this.isEdit);
      if (!this.isEdit && id == undefined) {
        this.currentIndex = 0;
        this.isEdit = true;
        this.getAllCamera();
      } else {
        this.$confirm("此操作将永久删除, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.$axios.delete(`/data/camera/deleteById/${id}`).then(res => {
              if (res.data == "success") {
                this.$message.success("删除成功");
              } else {
                this.$confirm(res.data, "提示", {
                  showCancelButton: false,
                  type: "warning",
                  center: true
                });
              }
              this.currentIndex = 0;
              this.getAllCamera();
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
      }
    },
    closeEdit(status) {
      console.log("currentIndex", this.currentIndex);
      if (status == "save") {
        this.currentIndex = 0;
      }
      this.isEdit = true;
      this.getAllCamera();
    }
  },
  components: {
    editCamera
  }
};
</script>

<style scoped lang="less">
.deploy_content {
  display: flex;
  height: 100%;
  .left {
    flex: 1;
    background-color: #ebebeb;
    aside {
      margin-top: 20px;
      padding: 0 20px;
      display: flex;
      justify-content: space-between;
      font-size: 14px;
      align-items: center;
      font-weight: 600;
      i {
        cursor: pointer;
      }
    }

    ul {
      margin-top: 10px;
      padding-left: 10px;
      li {
        cursor: pointer;
        padding-left: 10px;
        font-size: 12px;
        height: 30px;
        line-height: 30px;
        color: #4d4c4c;
        display: flex;
        justify-content: space-between;
        align-items: center;
        i {
          margin-right: 20px;
        }
        &:hover {
          background-color: #e0e0e0;
        }
      }
      .active {
        background-color: #e0e0e0;
      }
    }
  }
  .right {
    box-sizing: border-box;
    flex: 6;
  }
}
</style>



